import React, {useState, useEffect} from "react";
import './App.css';
import Recipe from './Recipes.js'

const App = () => {
  const APP_ID = 'baa73e7e';
  const APP_KEY = '5067f453a67620df83631457455876aa';

  const [recipes, setRecipes] = useState([])
  const [query, setQuery] = useState('salad')

  const [search, setSearch] = useState('')

  useEffect(() => {
      getRecipes()
    }, [query])

  const getRecipes = async () => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`)
    const data = await response.json()
    setRecipes(data.hits);
    console.log(recipes)
    if (setRecipes.length === 0) {
      console.log(0)
    }
  }

  const getSearch = e => {
    e.preventDefault();
    setQuery(search)
  }

  const updateSearch = e => {
    setSearch(e.target.value)
  }

  return (
    <div className="App">
    <form onSubmit={getSearch} className="search-form">
      <input autoComplete="off" placeholder="Search" onChange={updateSearch} className="search-bar" type="text" name="search" value={search} />
      <button className="search-button" type="submit">Search</button>
    </form>
    <div className="rec">
      {recipes.map(recipe => (
        <Recipe
         key={recipe.recipe.image}
         titile={recipe.recipe.label}
         calories={recipe.recipe.calories}
         thumbnail={recipe.recipe.image}
         ingredients={recipe.recipe.ingredients}
         />
        ))}
      </div>
    </div>
  );
}

export default App;
